import React from 'react'

export const Item = ({producto}) => {

    const {nombre, precio, urlImagen, descripcion} = producto;   

    return (        
        <div className="col-md-2 card" >
            <div className="card-header">
                <h4 className="text-primary">{nombre}</h4>
            </div>
            <img src={urlImagen} className="card-img-top" alt={nombre}/>              
            <div className="card-body">                
                {descripcion}
            </div>
            <div className="card-footer text-muted">
                <p className="card-text text-danger">{precio}</p>                
            </div>
        </div>
    )
}